import React, { useState, useEffect } from "react"
import { ThemeProvider } from 'styled-components';
import { GlobalStyle } from "./GlobalStyle.js"
import { Nav } from "./Components/Nav/Index"
import ScrollToTop from "./Components/ScrollToTop/Index"
import {Home} from "./Routes/Home"
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";


function App() {


  const [isOpenAtendOnline, setisOpenAtendOnline] = useState(false);
  const [isOpen, setIsOpen] = useState(false);
  const toggleCloseIcon = () => {
    setIsOpen(!isOpen)
  }

  const theme = {
    primary_color: "#fff",
    secundary_color: "#7AB531",
    ternary_color: "#F5C32E",
    font_color: "#333",
    font_padrao: "Roboto, sans-serif",
    font_secundaria: "Abel, sans-serif",

  };

  return (
    <>
      <ThemeProvider theme={theme}>
        <GlobalStyle />

        <Router basename={process.env.PUBLIC_URL}>
          <ScrollToTop>

            <Nav isOpen={isOpen} toggleCloseIcon={toggleCloseIcon} />

            <Switch>
               <Route path="/" exact>
                 <Home/>
               </Route>
            </Switch>
          </ScrollToTop>
        </Router>

      </ThemeProvider>


    </>
  );
}

export default App;
